import React, { useState, useRef } from 'react';
import './UploadTestForm.css';
import { Form, Input } from '@rocketseat/unform';
import UploadTestAPI from '../../api/UploadTestAPI';

const FileInput = React.forwardRef((props, ref) => (
  <input ref={ref} {...props} type="file" className="FileInput" />
));

const UploadTestForm = () => {
  const utteranceTestInput = useRef();
  const convoTestFileInput = useRef();
  const [utteranceTestFile, setUtteranceTestFile] = useState();
  const [convoTestFile, setConvoTestFile] = useState();

  const handleChangeForUtteranceTestFile = e => {
    setUtteranceTestFile(e.target.files[0]);
  };

  const handleChangeForConvoTestFile = e => {
    setConvoTestFile(e.target.files[0]);
  };

  return (
    <div className="UploadTestForm">
      <Form
        onSubmit={({ topic }) => {
          const data = new FormData();
          data.append('tests', convoTestFile);
          data.append('tests', utteranceTestFile);
          data.append('topic', topic);
          UploadTestAPI.uploadTest(data);
        }}
      >
        <div className="form-item">
          <Input name="topic" required label="Topico do Arquivo:" />
        </div>
        <div className="form-item file-item">
          <label htmlFor="utteranceTestFile">
            {utteranceTestFile?.name || 'Arquivo de Falas - Utterances'}
          </label>
          <FileInput
            ref={utteranceTestInput}
            id="utteranceTestFile"
            name="utteranceTestFile"
            type="file"
            required
            accept=".tsv"
            onChange={handleChangeForUtteranceTestFile}
          />
        </div>
        <div className="form-item file-item">
          <label htmlFor="convoTestFile">
            {convoTestFile?.name || 'Arquivo de Conversas - Convo'}
          </label>
          <FileInput
            ref={convoTestFileInput}
            id="convoTestFile"
            name="convoTestFile"
            type="file"
            required
            accept=".csv"
            onChange={handleChangeForConvoTestFile}
          />
        </div>
        <button type="submit">Upload</button>
      </Form>
    </div>
  );
};

export default UploadTestForm;
