/* eslint-disable no-console */
import axios from 'axios';
import ApiMock from '../mocks/Api';

const BASE_API = process.env.REACT_APP_BASE_API || 'http://localhost:3000';
const api = process.env.REACT_APP_REQUEST_MOCK
  ? ApiMock
  : axios.create({
      baseURL: BASE_API,
    });

console.log('api', process.env.REACT_APP_REQUEST_MOCK)

if (!process.env.REACT_APP_REQUEST_MOCK && !!process.env.REACT_APP_DEBUG) {
  api.interceptors.request.use(request => {
    console.info('[REQUEST]');
    console.info(request);
    return request;
  });

  api.interceptors.response.use(
    response => {
      console.info('[RESPONSE]');
      console.info(response);
      return response;
    },
    error => {
      console.warn('[RESPONSE - ERROR]');
      console.warn(error);

      return Promise.reject(error);
    }
  );
}

export default api;
