import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { green, red } from '@material-ui/core/colors';
import CardHeader from '@material-ui/core/CardHeader';
import Avatar from '@material-ui/core/Avatar';
import UploadTestAPI from '../../api/UploadTestAPI';
import IconButton from '@material-ui/core/IconButton';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import CheckIcon from '@material-ui/icons/Check';
import LinearProgress from '@material-ui/core/LinearProgress';
import { useRef } from 'react';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        marginTop: theme.spacing(4),
        padding: theme.spacing(4),
        height: 220,
        width: 300
    },
    error: {
        backgroundColor: red[500],
    },
    success: {
        color: green[500],
    },
    executeTestsCard: {
        marginTop: theme.spacing(4),
        padding: theme.spacing(4),
    },
    loading: {
        color: green[500],
        marginTop: theme.spacing(4),
        width: '100%',
    }
}));

export default function SpacingGrid() {
    const [spacing] = React.useState(2);
    const classes = useStyles();
    const [tests, setTestState] = useState([])
    const [progress, setProgress] = React.useState(0);
    const [isRunning, setRunningState] = React.useState(false);
    let btnRef = useRef();

    useEffect(() => {
        const schedule = createUpdateSchedule()
        isRunningTestSuiteCheck()
        const checkInterval = setInterval(() => {
            isRunningTestSuiteCheck()
            isDoneAllTests(schedule, checkInterval)
        }, 20000);
    }, [])

    const updateTests = () => {
        UploadTestAPI.getAllTests().then(tests => {
            setTestState(tests)
        })
    }
    const avatar = (error) => {
        if (error === 0) {
            return <CheckIcon className={classes.success} />
        } else {
            return <Avatar aria-label="recipe" className={classes.error}>{error}</Avatar>
        }
    }
    const runSingleTest = (test) => {
        setRunningState(true)
        setTimeout(()=>updateTests(), 1000)
        
        UploadTestAPI.executeSingleTest(test.utterance).then(() => {
            isRunningTestSuiteCheck()
        }).catch((e) => {
            alert('Some test is already running')
        })
    }

    const createUpdateSchedule = () => {
        updateTests()
        return setInterval(() => {
            updateTests()
        }, 5000);
    }

    const isRunningTestSuiteCheck = () => {
        UploadTestAPI.getStateProcessing().then(({ data }) => {
            console.log(data)
            if (data === 'done' || data === 'fail' || data === 'server just started') {
                setRunningState(false)
            } else {
                setRunningState(true)
            }
        })
    }

    const isDoneAllTests = (schedule, checkInterval) => {
        UploadTestAPI.getStatusProccessing().then(({ data }) => {
            const { percent } = data
            if (parseFloat(percent) === 100) {
                clearInterval(schedule)
                clearInterval(checkInterval)
            } else {
                setProgress(percent)
            }
        }).catch((e) => {
            clearInterval(schedule)
            clearInterval(checkInterval)
            alert('Server failed on retrieve status')
        })
    }

    const handleExecuteTests = async () => {
        setRunningState(true)

        UploadTestAPI.executeAllTests().then((res) => {
            alert('All tests ended')
        })
    };

    const cardHeader = (test, index) => {
        return <CardHeader
            avatar={test.existsReport ? avatar(test.failures) : false}
            action={
                <IconButton aria-label="run tests" onClick={() => {
                    runSingleTest(test, index)
                }}>
                    <PlayArrowIcon />
                </IconButton>
            }
            title={test.utterance}
            subheader={test.existsReport ? test.numTests + ' tests executed' : 'Running'}
        />
    }

    const cardBody = (test, index, text) => {
        return <Card className={classes.root} key={index}>
            {cardHeader(test, index)}
            <CardContent>
                <Typography variant="body2" color="textSecondary" component="p">
                    {text}
                </Typography>
            </CardContent>
        </Card>
    }

    const bottomCardSide = (test, index) => {
        if (test.existsReport) {
            return <Card className={classes.root} key={index}>
                {cardHeader(test, index)}
                <CardContent>
                    <Typography variant="body2" color="textSecondary" component="p">
                        Tests passing: {test.passes}
                    </Typography>
                    <Typography variant="body2" color="textSecondary" component="p">
                        Test failures: {test.failures}
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button size="small" color="primary" href={test.pageLink}>
                        Test suite page
                                </Button>
                    <Button size="small" color="primary" href={test.pageReportLink}>
                        Page json report
                                </Button>
                </CardActions>
            </Card>
        } else {
            if (isRunning) {
                return cardBody(test, index, 'This utterance is running now')
            } else {
                return cardBody(test, index, 'This test report was not created')
            }
        }
    }
    const loading = () => {
        if (isRunning) {
            return <div className={classes.loading}>
                <LinearProgress variant="determinate" value={progress} />
            </div>
        } else {
            return <div></div>
        }
    }
    return (
        <Card className={classes.executeTestsCard}>
            <Typography>
                Execute todos os testes clicando no botão abaixo:
            </Typography>
            <Button
                ref={btnRef}
                variant="contained"
                onClick={handleExecuteTests}
                color="primary"
                disabled={isRunning}
                size="large"
            >
                Executar Testes
            </Button>
            {loading()}
            <Grid item xs={12}>
                <Grid container justify="center" spacing={spacing}>
                    {tests.map((test, index) => (
                        <Grid key={index} item>
                            {bottomCardSide(test, index)}
                        </Grid>
                    ))}
                </Grid>
            </Grid>
        </Card>
    );
}