import React, { useCallback, useContext } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

import { AuthContext } from '../../context/AuthContext';

import './ChatBotAppBar.css';

const BASE_API = process.env.REACT_APP_BASE_API || 'http://localhost:3000';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  logoutButton: {
    color: '#fff',
  },
}));
function ChatBotAppBar({ history }) {
  const classes = useStyles();

  const { signOut } = useContext(AuthContext);

  const handleLogout = useCallback(async () => {
    await signOut();
    history.push('/');
  }, [signOut, history]);

  return (
    <AppBar position="static">
      <Toolbar className={classes.root}>
        <Typography variant="h5" className={classes.title}>
          ChatBot de People Support
        </Typography>
        <Button className={classes.logoutButton} onClick={handleLogout}>
          Sair
        </Button>
        <a target="_blank" rel="noreferrer" href={BASE_API + '/test-suite-descriptor'}>
          <Button className="link">Ver Resultados de Testes</Button>
        </a>
      </Toolbar>
    </AppBar>
  );
}

ChatBotAppBar.propTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
};

export default withRouter(ChatBotAppBar);
