import React, {
  useState,
  useCallback,
  useContext,
  useRef,
  useEffect,
} from 'react';
import {
  Container,
  CardContent,
  Card,
  Button,
  Typography,
  LinearProgress,
  Snackbar,
} from '@material-ui/core';

import MuiAlert from '@material-ui/lab/Alert';

import { ValidationError } from 'yup';

import styles from './styles';
import Input from '../../components/Input';

import { AuthContext } from '../../context/AuthContext';

import logo from '../../assets/logo.png';
import schema from '../../validations/login-schema';
import { TransformValidationError } from '../../services/tranformers';

const Login = () => {
  const classes = styles();

  const isMountedRef = useRef(null);

  useEffect(() => {
    return () => {
      isMountedRef.current = false;
    };
  }, [isMountedRef]);

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [generalError, setGeneralError] = useState('');

  const { signIn } = useContext(AuthContext);
  const handleSubmit = useCallback(
    async e => {
      e.preventDefault();
      setErrors([]);

      isMountedRef.current = true;

      try {
        await schema.validate(
          { email, password },
          {
            abortEarly: false,
          }
        );

        setLoading(true);

        await signIn({ email, password });
      } catch (err) {
        if (err instanceof ValidationError)
          return setErrors(TransformValidationError(err));

        if (err.response?.status === 422) {
          return setGeneralError(
            'Login ou senha inválidos, verifique seus dados.'
          );
        }

        setGeneralError(
          'Falha ao efetuar login, por favor tente novamente mais tarde.'
        );
      } finally {
        if (isMountedRef.current) {
          setLoading(false);
        }
      }
    },
    [email, password, setErrors, signIn, setLoading, setGeneralError]
  );

  return (
    <Container maxWidth="sm">
      <Card className={classes.root}>
        {loading && <LinearProgress />}
        <CardContent
          className={`${classes.content} ${loading && classes.contentLoading}`}
        >
          <img
            className={classes.logo}
            src={logo}
            alt="Logo marca da aplicação"
          />

          <Typography variant="h6">Fazer login</Typography>
          <Typography variant="subtitle1" className={classes.subtitle}>
            Informe suas credenciais
          </Typography>
          <form
            className={classes.form}
            noValidate
            autoComplete="off"
            onSubmit={handleSubmit}
          >
            <Input
              type="email"
              required
              name="email"
              id="email-required"
              label="E-mail"
              errorMessage={errors.email}
              onChange={e => setEmail(e.target.value)}
            />
            <Input
              required
              id="senha-required"
              label="Senha"
              type="password"
              name="password"
              errorMessage={errors.password}
              onChange={e => setPassword(e.target.value)}
            />
            <Button
              type="submit"
              disabled={loading}
              variant="contained"
              color="primary"
            >
              Acessar
            </Button>
          </form>
          <Snackbar
            open={!!generalError}
            autoHideDuration={2000}
            onClose={() => setGeneralError('')}
          >
            <MuiAlert elevation={6} variant="filled" severity="error">
              {generalError}
            </MuiAlert>
          </Snackbar>
        </CardContent>
      </Card>
    </Container>
  );
};

export default Login;
