/* eslint-disable no-console */
import SessionsResponseDataMock from './Sessions';

const responses = {
  '/sessions': SessionsResponseDataMock,
};

let config = {
  headers: {},
};

const ApiMock = {
  post: (pathUrl, ...rest) => {
    return new Promise(resolve => {
      console.log(
        `[POST] - Mock request to ${pathUrl} params: ${JSON.stringify(rest)}`
      );
      console.log(`[HEADERS] - ${JSON.stringify(config)}`);

      setTimeout(() => {
        console.log(`[RESPONSE] - ${JSON.stringify(responses[pathUrl].post)}`);
        return resolve(responses[pathUrl].post);
      }, 3500);
    });
  },
  get: pathUrl => {
    return new Promise(resolve => {
      console.log(`[GET] - Mock request to ${pathUrl}`);
      console.log(`[HEADERS] - ${JSON.stringify(config)}`);
      setTimeout(() => {
        console.log(`[RESPONSE] - ${JSON.stringify(responses[pathUrl].get)}`);
        return resolve(responses[pathUrl].get);
      }, 3500);
    });
  },
  interceptors: {
    request: {
      use: callback => {
        config = callback(config);
      },
    },
  },
};

export default ApiMock;
