import React, { createContext, useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import jwt from 'jsonwebtoken';
import api from '../api';

const storageNames = {
  token: '@ChatbotPeopleSupport:token',
};

const AuthContext = createContext({});

const AuthProvider = ({ children }) => {
  const [data, setData] = useState(() => {
    const token = localStorage.getItem(storageNames.token);
    if (!token) {
      return {};
    }

    try {
      const user = jwt.verify(token, process.env.REACT_APP_JWT_PUBLIC_KEY);

      api.interceptors.request.use(config => {
        return { ...config, headers: { Authorization: `${token}` } };
      });

      return { token, user };
    } catch (e) {
      localStorage.removeItem(storageNames.token);
      return {};
    }
  });

  const signOut = useCallback(async () => {
    return new Promise(resolve => {
      localStorage.removeItem(storageNames.token);

      api.interceptors.request.use(async config => {
        return { ...config, headers: {} };
      });
      setData({});

      return resolve();
    });
  }, [setData]);

  const signIn = useCallback(
    async ({ email, password }) => {
      const response = await api.post('/sessions', {
        email,
        password,
      });
      const { token, user } = response.data;

      localStorage.setItem(storageNames.token, token);

      api.interceptors.request.use(config => {
        return { ...config, headers: { Authorization: `Bearer ${token}` } };
      });

      setData({ token, user });
    },
    [setData]
  );

  return (
    <AuthContext.Provider value={{ user: data.user, signIn, signOut }}>
      {children}
    </AuthContext.Provider>
  );
};

AuthProvider.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};

export { AuthContext, AuthProvider };
