import api from './index';
const BASE_API = process.env.REACT_APP_BASE_API || 'http://localhost:3000';

const UploadTestAPI = {
  uploadTest(data) {
    api
      .post(`/tests`, data)
      .then(response => {
        alert('Upload realizado com sucesso!');
      })
      .catch(err => alert('Falha ao realizar upload.'));
  },
  async executeAllTests() {
    return api.post(`/tests/execute-tests`, {})
  },
  async executeSingleTest(reportIndex) {
    return api.post(`/tests/execute-tests/${reportIndex}`, {})
  },
  async getStatusProccessing() {
    return api.get('tests/status/percentage')
  },
  async getStateProcessing() {
    return api.get('tests/status')
  },
  async getReport(reportIndex) {
    return await api.get(`/tests/${reportIndex}`)
  },
  async getAllTests() {
    const { data } = await api.get(`/tests`)
    const { tests } = data

    return await this.instanceTestObject(tests);
  },
  async instanceTestObject(tests) {
    let testObject = []
    for (const test of tests) {
      try {
        const report = await this.getReport(test);
        const { stats } = report.data;

        testObject.push({
          utterance: test,
          existsReport: true,
          numTests: stats.tests,
          passes: stats.passes,
          failures: stats.failures,
          pageLink: `${BASE_API}/${test}.html`,
          pageReportLink: `${BASE_API}/${test}.json`
        });
      } catch (error) {
        testObject.push({
          utterance: test,
          existsReport: false
        });
      } finally {
        setTimeout(() => { }, 500)
      }
    }
    return testObject
  }
};
export default UploadTestAPI;

